import React, { Component } from 'react';
import './App.css';
import "./nprogress.css";
import EventList from './EventList';
import CitySearch from './CitySearch';
import NumberOfEvents from './NumberOfEvents';
import { extractLocations, getEvents } from './api';

class App extends Component {
  state = {
    events:[],
    locations:[],
    currentLocation:'all',
    numberofevents: 20,
 
  }

 
  componentDidMount() {
    this.mounted = true;
    getEvents().then((events) => {
      if (this.mounted) {
        const locationEvents = events.slice(0, this.state.numberofevents);
     
        this.setState({ events: locationEvents, locations: extractLocations(events), });
      }
    });
  }
  

  componentWillUnmount() {
    this.mounted = false;
  }

 
  
  updateEvents = (location, eventCount) => {
    getEvents().then((events) => {
   
      let  locationEvents;
      if(!eventCount){
         locationEvents = (location === 'all') ?
          events :
          events.filter((event) => event.location === location);
          locationEvents = locationEvents.slice(0, this.state.numberofevents);
      }else{
          var currentLocation = this.state.currentLocation;
        
          locationEvents = (location === 'all') ?
          events :
          events.filter((event) => event.location === currentLocation);
          locationEvents = locationEvents.slice(0, eventCount);
      }
   
      this.setState({
        events: locationEvents,
        numberofevents:eventCount,
        currentLocation:location,
      });
    
    });
  }

  

  render(){
  return (
    <div className="App">
      <CitySearch locations = {this.state.locations}  updateEvents={this.updateEvents}/>
      <NumberOfEvents numberofevents = {this.state.numberofevents} updateEvents = {this.updateEvents}/>
    <EventList events={this.state.events} />
  
    
     
    </div>
  );
};
}

export default App;
